.main-footer {
  background-color: $color-default-blue;
  padding: 13px 103px;
  margin-top: 25px;

  @media (max-width: $vp-1366) {
    padding: 2px 45px;
    margin-top: 100px;
  }

  @media (max-width: $vp-768) {
    padding: 40px 15px 36px;
    margin-top: 46px;
  }
}

.main-footer__wrapper {
  width: 100%;
  max-width: (1366px - 206px);
  margin: 0 auto;
  display: flex;
  justify-content: space-between;

  @media (max-width: $vp-768) {
    flex-direction: column;
    align-items: flex-start;
    gap: 14px;
  }
}
